<template>
  <div class="my-container">
    <!-- 顶部登录/注册盒子 -->
    <!-- 已登陆 -->
    <div v-if="user" class="header user-info">
      <!-- 基本信息 -->
      <div class="base-info">
        <div class="left">
          <!-- 头像设置 -->
          <van-image
            class="avatar"
            :src="userInfo.photo"
            round
            fit="cover"
            />
            <!-- 用户昵称 -->
          <span class="name">{{ userInfo.name }}</span>
        </div>
        <div class="right">
          <!-- 编辑资料按钮 -->
          <van-button size="mini" round type="default" to="/user">编辑资料</van-button>
        </div>
      </div>
      <!-- 粉丝、关注 - 个人数据 -->
      <div class="data-stats">
        <div class="data-item">
          <span class="count">{{ userInfo.art_count }}</span>
          <span class="text">头条</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userInfo.follow_count }}</span>
          <span class="text">关注</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userInfo.fans_count }}</span>
          <span class="text">粉丝</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userInfo.like_count }}</span>
          <span class="text">获赞</span>
        </div>
      </div>
    </div>
    <!-- 去登录 -->
    <div v-else class="header not-login">
      <div class="login-btn" @click="$router.push('/login')">
        <img class="mobile-img" src="../../assets/mobile.png" alt="" />
        <span class="text">登录 / 注册</span>
      </div>
    </div>

    <!-- 宫格导航 -->
    <van-grid class="grid-nav mb-9" :column-num="2" clickable>
      <van-grid-item class="grid-item">
        <i slot="icon" class="toutiao toutiao-shoucang"></i>
        <span slot="text" class="text">收藏</span>
      </van-grid-item>
      <van-grid-item class="grid-item">
        <i slot="icon" class="toutiao toutiao-lishi"></i>
        <span slot="text" class="text">历史</span>
      </van-grid-item>
    </van-grid>
    <!-- /宫格导航 -->

    <!-- 单元格导航 -->
    <van-cell title="消息通知" is-link />
    <van-cell class="mb-9" title="小智同学" is-link />
    <!-- 退出 -->
    <van-cell clickable @click="onLogout" v-if="user" class="logout-cell" title="退出登录" center />
    <!-- 单元格导航 -->

  </div>
</template>

<script>
// 通过内置 mapState 方法 获取vuex中的state全局数据
import { mapState } from 'vuex'
import { getUserInfoAPI } from '../../api/index.js'
export default {
  name: 'mine-',
  data () {
    return {
      userInfo: {}
    }
  },
  created () {
    // 如果用户登录了，则请求加载用户信息数据
    if (this.user) {
      this.loadUserInfo()
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    // 退出登录
    onLogout () {
      // 退出提示
      // 在组件中需要使用 this.$dialog 来调用弹框组件
      this.$dialog.confirm({
        title: '确认退出吗？'
      }).then(() => {
        // on confirm
        // 确认退出：清除登录状态（容器中的 user + 本地存储中的 user）
        this.$store.commit('setUser', null)
      }).catch(() => {
        // on cancel
        console.log('取消执行这里')
      })
    },
    // 获取用户详细信息
    async loadUserInfo () {
      try {
        const res = await getUserInfoAPI()
        console.log(res.data.data)
        this.userInfo = res.data.data
      } catch (err) {
        this.$toast('获取数据失败，请稍后重试')
      }
    }
  }
}
</script>
